let pageTop = document.getElementsByClassName('pageTop')[0];
function pageTopFunc() {
	const pageTop = document.getElementsByClassName('pageTop')[0];
	document.addEventListener('scroll', (e) => {
		let wH = window.pageYOffset;
		let sH = document.documentElement.scrollTop;
		let scrollTop = wH || sH;
		if (scrollTop > 100) {
			pageTop.classList.add('positionFix');
			pageTop.classList.remove('positionRelative');
			pageTop.style.right =  "20px";
			pageTop.style.bottom =  "20px";
		} else {
			pageTop.classList.add('positionAbsolte');
			pageTop.classList.remove('positionFix');
			pageTop.style.right = "-100%";
			pageTop.style.bottom = "0";
		}
	});
}
pageTopFunc();

let jsScroll = document.getElementsByClassName('jsScroll');
for (let i = 0; i < jsScroll.length; i++) {
	jsScroll[i].addEventListener('click', function () {
		let y = document.body.scrollTop || document.documentElement.scrollTop;
		let scrollSpeed = 7;
		let direction = this.dataset.scroll;
		let scrollTarget = 0;
		if (direction != 'up') {
			scrollTarget = document.getElementById(direction).getBoundingClientRect().top;
		};
		
		let targetY = scrollTarget;
		
		scrollToAnimation(0, y, 0, targetY, scrollSpeed, direction);
		return false;
	});
}

// 指定した座標へスクロールアニメーション
function scrollToAnimation(x, y, targetX, targetY, scrollSpeed, direction) {
	let scTop = direction === 'up' ? Math.floor(y - (y / (scrollSpeed * 2))) : Math.floor(y + (y / (scrollSpeed * 2) + 1));
	let scrollTimer = setTimeout(function () { scrollToAnimation(x, scTop, targetX, targetY, scrollSpeed, direction) }, scrollSpeed);

	if (direction === 'up') {
		if (y > targetY) {
			window.scrollTo(x, scTop);
			scrollTimer;
		} else {
			clearTimeout(scrollTimer);
			window.scrollTo(x, targetY);
		}
	} else {
		if (y < targetY) {
			if (scTop == 0) scTop++;
			window.scrollTo(x, scTop);
			scrollTimer;
		} else {
			clearTimeout(scrollTimer);
			window.scrollTo(x, targetY);
		}
	}
}



